<template>
  <div class="coupon-alert">
    <div
      class="alert alert-dismissible d-flex justify-content-center align-items-center rounded-0"
      :class="'alert-' + item.status"
      v-for="(item, i) in messages"
      :key="i"
    >
      <strong class="text-center alert-txt cartProduct_txt">{{ item.message}}</strong>
      <button type="button" class="close" @click="removeMessage(i)" aria-label="Close">
        <span style="font-size:20px; color:#f31e1e" aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      messages: [],
      qty: ''
    };
  },
  methods: {
    updateMessage (message, status) {
      const timestamp = Math.floor(new Date() / 1000);
      this.messages.push({
        message,
        status,
        timestamp
      });
      this.removeMessageWithTiming(timestamp);
    },
    removeMessage (num) {
      this.messages.splice(num, 1);
    },
    removeMessageWithTiming (timestamp) {
      const vm = this;
      setTimeout(() => {
        vm.messages.forEach((item, i) => {
          if (item.timestamp === timestamp) {
            vm.messages.splice(i, 1);
          }
        });
      }, 2000);
    }
  },
  created () {
    const vm = this;

    // 自定義名稱 'messsage:push'
    // message: 傳入參數
    // status: 樣式，預設值為 warning
    vm.$bus.$on('coupon:push', (message, status = 'dark') => {
      vm.updateMessage(message, status);
    });
    // vm.$bus.$emit('message:push');
  }
};
</script>

<style lang="scss" scoped>
.coupon-alert {
  position: fixed;
  width: 250px;
  top: 80px;
  right: 0px;
  z-index: 1100;
}

.alert {
  background-color: rgba(236, 236, 236, 0.6);
  border: 0;
}

.alert-txt {
  font-size: 20px;
  font-family: "Anton", sans-serif;
  color: #f31e1e;
}
</style>
